/* -------------------------------- 
---- Phones & Tablet Media Queries ---------
----------------------------------- */

// iPhone 5 & 5S in portrait & landscape
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 568px) { 
/* STYLES GO HERE */
	.main-header {
		display: block;
		padding: 0;
		height: 62px;
	}
	.site-logo {
		display: inline-block;
		margin-top: 13px;
		margin-left: 10px;
		img {
			width: 40px;
		}
	}
	.nav {
		width: initial;
		padding-top: 20px;
		padding-bottom: 20px;
	}	
}

@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 568px) 
and (orientation : portrait) { 
	/* STYLES GO HERE */ 
	.mustard-rover {
		height: auto;
	}
}
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 568px) 
and (orientation : landscape) { 
	/* STYLES GO HERE */
	.main-footer {
		padding-top: 20px;
		padding-bottom: 10px;
		p {
			margin: 0;
		}
	}
}


@media screen and (device-width: 640px) and (device-height: 360px) and (-webkit-device-pixel-ratio: 3) {

	/* Style */ 
	.main-footer {
		padding-top: 10px;
		padding-bottom: 10px;
	}

}

@media screen and (device-width: 640px) and (device-height: 360px) and (-webkit-device-pixel-ratio: 4) {

	/* Style */ 
	.main-footer {
		padding-top: 10px;
		padding-bottom: 10px;
	}

}
// iPad in portrait & landscape
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px)  { 
	/* STYLES GO HERE */
	.main-header {
		padding: 0.45em 1.3em;
	}
	.site-logo {
		img {
			width: 40px;
		}
	}
	.nav {
		width: initial;
		padding-top: 20px;
		padding-bottom: 20px;
	}	
}

@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : landscape) { 
	/* STYLES GO HERE */
	.site-logo {
		img {
			width: 30px;
			margin-top: 10px;
		}
	}
	.nav {
		padding-top: 10px;
		padding-bottom: 10px;
	}
}